.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  background-color: $white;
}

.logo {
  display: block;
  height: 40px;
  width: 40px;

  &__image {
    height: 100%;
    width: 100%;
  }
}

.nav {
  display: block;

  &__list {
    display: flex;
    padding: 0;
    margin: 0;
    list-style: none;
  }

  &__item {
    margin-left: 20px;

    &:first-child {
      margin-left: 0;
    }
  }

  &__link {
    position: relative;

    display: flex;
    align-items: center;

    height: 60px;

    font-size: 12px;
    font-weight: 500;
    white-space: nowrap;
    text-decoration: none;
    text-transform: uppercase;
    color: $main-accent;

    transition: color 0.3s ease;

    &:hover {
      color: $color-link;
    }
  }
}

.is-active {
  color: $color-link;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;

    height: 4px;
    border-radius: 2px;
    background-color: $color-link;
  }
}
